import React from 'react'
import {
    WrapperDiv
  } from "./styled-index";
import LoginFormCompo from './LoginForm'
import LoginImgCompo from './LoginImg'

function LoginComponent() {
  return (
    <>
        <WrapperDiv>
            <LoginFormCompo/>
            <LoginImgCompo/>
        </WrapperDiv>
    </>
  )
}

export default LoginComponent