import styled from "styled-components";

export const SellingWrapper = styled.div`
  width: 39%;
  max-width: 600px;
  padding: 5px 10px;
  background: #ffffff;
  border-radius: 10px;
`;

export const SellingCard = styled.div`
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin: 7px 0;

  img {
    border-radius: 15px;
  }
`;

export const SellingInfo = styled.div`
  padding: 0 0 0 15px;

  h2 {
    font-family: "DM Sans";
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 22px;
    color: #06152b;
    margin: 10px 0;
  }

  p{
    margin: 10px 0;
    font-family: "DM Sans";
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 22px;
    color: #06152b;
  }
`;

