import styled from "styled-components";

export const WrapperDiv = styled.div`
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    overflow: hidden;
`;

export const LogoBlock = styled.div`
    width: 100%;
    margin: 20px 0;
`;

export const LoginFormBlock = styled.div`
    width: 30%;
    height: 100%;
    padding: 182px 50px;
    background: #FFFFFF;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
`;

export const LoginImgBlock = styled.div`
    width: 70%;
    height: 100vh;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #F1F4FA;

`;

export const LoginForm =  styled.form`
    width: 100%;
`;

export const Input = styled.input`
  width: 90.5%;
  padding: 10px 16px;
  border: 2px solid transparent;
  background: #F1F4FA;
  border-radius: 10px;
  outline: none;
  margin-top: 6px;
  margin-bottom: 10px;
  color: #06152B;
  font-family: 'DM Sans', sans-serif;
  :focus{
    border: 2px solid #297D3B
  }
  ::placeholder{
    color: #06152B
  }
`;

export const Label = styled.label`
    color: #06152B;
    font-weight: 500;
    font-size: 16px;
    line-height: 21px;
    color: #06152B;
    margin-top: 10px;
    font-family: 'DM Sans', sans-serif;
`;

export const Button = styled.button`
    width: 100%;
    margin: 30px 0;
    background: #297D3B;
    border-radius: 10px;
    font-weight: 500;
    font-size: 16px;
    line-height: 21px;
    color: #FFFFFF;
    padding: 10px 16px;
    border: none;
    outline: none;
    cursor: pointer;
    transition: 0.5s;
    font-family: 'DM Sans', sans-serif;
`;

export const Title = styled.h1`
    font-weight: 700;
    font-size: 25.63px;
    line-height: 33px;
    color: #06152B;
    text-align: center;
    font-family: 'DM Sans', sans-serif;
`;

export const Img = styled.img`
   margin: 0 auto;
   display: flex;
   align-items: center;
`;














