import { Table } from "./styled-index";
import { useDispatch, useSelector } from "react-redux";
import { useEffect, useState } from "react";
import { GetUsers } from "../../store/client/users";
import PutUser from "./put";
function UsersComp() {
  const label = { inputProps: { "aria-label": "Checkbox demo" } };
  const dipatch = useDispatch();
  useEffect(() => {
    dipatch(GetUsers());
  }, []);
  const Data = useSelector((state) => state.users.getUsers?.Data);
  return (
    <>
      <Table>
        <thead>
          <tr>
            <th>
              <h3>Invoice Id</h3>
            </th>
            <th>
              <h3>Name</h3>
            </th>
            <th>
              <h3>Email</h3>
            </th>
            {/* <th>
              <h3>Date</h3>
            </th> */}
            <th>
              <h3>Status</h3>
            </th>
        
          </tr>
        </thead>
        <tbody>
          {Data.map((elem, index) => (
            <tr>
              <td>
                <h3>{index}</h3>
              </td>
              <td>
                {/* <div className="infor"> */}
                {/* <img src={elem.img} alt="photo" /> */}
                <h3>{elem.username}</h3>
                {/* </div> */}
              </td>
              <td>
                <h3>
                  <i class="bx bxs-envelope"></i>
                  {elem.email}
                </h3>
              </td>
              {/* <td>
                <h3>
                  <i class="bx bx-calendar"></i>12 Dec, 2020
                </h3>
              </td> */}
              <td>
                <PutUser status={elem.status} userId={elem.user_id} />
              </td>
            </tr>
          ))}
        </tbody>
  
      </Table>
    </>
  );
}

export default UsersComp;
