import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import Modal from "@mui/material/Modal";
import { DeleteDiv } from "./styled-index";
import { useDispatch, useSelector } from "react-redux";
import { DeleteNews, GetNews } from "../../../store/client/news";

const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  bgcolor: "background.paper",
  border: "2px solid #000",
  boxShadow: 24,
  p: 4,
};

function DeleteModal({ open, setOpen, handleClose, handleOpen, del_id }) {
  const dispatch = useDispatch();
  const id = del_id;
  const Delete = async (e) => {
    await dispatch(DeleteNews(id));
    dispatch(GetNews());
    handleClose();
  };
  return (
    <div>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <DeleteDiv>
            <h2>    ?</h2>
            <div className="buttons">
              <button className="del" onClick={Delete}>
                
              </button>
              <button className="put" onClick={handleClose}>
                
              </button>
            </div>
          </DeleteDiv>
        </Box>
      </Modal>
    </div>
  );
}

export default DeleteModal;
