import styled from "styled-components";


export const Title = styled.h2`
    font-weight: 700;
    font-size: 27px;
    line-height: 35px;
    color: #06152B;
`
export const CardWrapper = styled.div`
   width: 100%;
   display: flex;
   align-items: center;
   justify-content: space-between;
`

export const Card = styled.div`
    background: #FFFFFF;
    border-radius: 10px;
    width: auto;
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    h4{
        margin: 0;
        font-weight: 700;
        font-size: 24px;
        line-height: 31px;
        color: #06152B;
        opacity: 0.7;
        font-family: 'DM Sans', sans-serif;
    }

    span{
        font-weight: 400;
        font-size: 15px;
        line-height: 20px;
        color: #06152B;
        opacity: 0.7;
        font-family: 'DM Sans', sans-serif;
    }
`

export const CardLogo = styled.div`
    background: #99B2C6;
    margin: 0 25px 0 0;
    padding: 20px;
    border-radius: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #F2F5F7;

    img{
        width: 21px;
        height: 20px
    }
`

export const InputWrap = styled.div`
    display: flex;
    align-items-center;
    margin: 0 0 10px 0;

    input{
        background: #FFFFFF;
        font-family: 'DM Sans';
        border-radius: 6px;
        font-weight: 400;
        font-size: 15.0213px;
        line-height: 20px;
        color: #06152B;
        opacity: 0.7;
        padding: 8px 17px;
        outline: none;
        border: none;

        &:nth-child(1) {
            margin: 0 15px;
        }
    }
`