import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { GetSuperUsers } from "../../store/client/superusers";
import DeleteModal from "./delete";
import PutSuperUser from "./put";
import { Table } from "./styled-index";

function SuperComp() {
  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  const [delid, setDle_id] = useState();
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(GetSuperUsers());
  }, []);
  const Data = useSelector((state) => state?.superusers?.getSuperUsers?.Data);
  return (
    <>
      <Table>
        <thead>
          <tr>
            <th>
              <h3>Invoice Id</h3>
            </th>
            <th>
              <h3>CompanyName</h3>
            </th>
            <th>
              <h3>Email</h3>
            </th>
            <th>
              <h3>Number</h3>
            </th>
            <th>
              <h3>Experience</h3>
            </th>
            <th>
              <h3>TelegramName</h3>
            </th>
            <th>
              <h3>InstagramName</h3>
            </th>
            <th>
              <h3>Status</h3>
            </th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {Data.map((elem, index) => (
            <tr>
              <td>
                <h3>{index}</h3>
              </td>
              <td>
                {/* <div className="infor"> */}
                {/* <img src={elem.img} alt="photo" /> */}
                <h3>{elem.company_name}</h3>
                {/* </div> */}
              </td>
              <td>
                <h3>
                  <i class="bx bxs-envelope"></i>
                  {elem.email}
                </h3>
              </td>
              <td>
                <h3>{elem.phone_number}</h3>
              </td>
              <td>
                <h3>{elem.experience}</h3>
              </td>
              <td>
                <h3>{elem.tg_username}</h3>
              </td>
              <td>
                <h3>{elem.inst_username}</h3>
              </td>
              <td>
                <PutSuperUser status={elem.status} userId={elem.id} />
              </td>
              <td>
                <button
                  className="delete"
                  onClick={() => {
                    setDle_id(elem.id);
                    handleOpen();
                  }}
                >
                  
                </button>
              </td>
            </tr>
          ))}
        </tbody>
        <DeleteModal
          open={open}
          setOpen={setOpen}
          handleClose={handleClose}
          handleOpen={handleOpen}
          delid={delid}
        />
      </Table>
    </>
  );
}

export default SuperComp;
