import { useEffect, useRef, useState } from "react";
import { Col, Row } from "react-grid-system";
import { useDispatch, useSelector } from "react-redux";
import { GetNews, PostNews, UploadImage } from "../../store/client/news";
import DeleteModal from "./delete";
import PostNew from "./post";
import PutModal from "./put";
import { Wrapper } from "./styled-index";

function NewsComp() {
  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const [open2, setOpen2] = useState(false);
  const handleOpen2 = () => setOpen2(true);
  const handleClose2 = () => setOpen2(false);

  const [open3, setOpen3] = useState(false);
  const handleOpen3 = () => setOpen3(true);
  const handleClose3 = () => setOpen3(false);

  const [del_id, setDle_id] = useState("");
  const [put_id, setPut_id] = useState("");
  const dispatch = useDispatch();
  const img_post = useSelector((state) => state.news.uploadProfile);
  const img = img_post.data;
  const HandelPost = async (e) => {
    await dispatch(PostNews({ img }));
    dispatch(GetNews());
    handleClose2();
  };
  useEffect(() => {
    dispatch(GetNews());
  }, []);
  const Data = useSelector((state) => state.news.getNews?.Data);
  return (
    <>
      <Wrapper>
        <div className="header">
          <h2>News</h2>
          <button className="editBtn" onClick={handleOpen2}>
            Add+
          </button>
        </div>
        <PostNew
          HandelPost={HandelPost}
          open={open2}
          setOpen={setOpen2}
          handleClose={handleClose2}
          handleOpen={handleOpen2}
        />
        <div className="imgs">
          <Row style={{ margin: 0 }}>
            {Data.map((elem, index) => (
              <Col lg={12} key={index}>
                <div className="cols">
                  <img src={elem.img} alt="photo" />
                  <div className="buttons">
                    <button
                      className="del"
                      onClick={() => {
                        handleOpen();
                        setDle_id(elem.id);
                      }}
                    >
                      Delete
                    </button>
                    <button
                      onClick={() => {
                        handleOpen3();
                        setPut_id(elem.id);
                      }}
                      className="put"
                    >
                      Update
                    </button>
                  </div>
                </div>
              </Col>
            ))}
          </Row>
          <DeleteModal
            open={open}
            setOpen={setOpen}
            handleClose={handleClose}
            handleOpen={handleOpen}
            del_id={del_id}
          />
          <PutModal
            open={open3}
            setOpen={setOpen3}
            handleClose={handleClose3}
            handleOpen={handleOpen3}
            put_id={put_id}
          />
        </div>
      </Wrapper>
    </>
  );
}

export default NewsComp;
