import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";
import { API_URL } from "../../servies";

export const PostUserCategory = createAsyncThunk(
  "UserCategory/post",
  async (body) => {
    return axios.post(`${API_URL}/user_category`, body).then((res) => res);
  }
);
export const GetUserCategory = createAsyncThunk(
  "UserCategory/get",
  async () => {
    return await axios
      .get(`${API_URL}/user_category`)
      .then((response) => response.data);
  }
);
export const DeleteUserCategory = createAsyncThunk(
  "UserCategory/delete",
  async (id) => {
    return await axios
      .delete(`${API_URL}/user_category/${id}`)
      .then((response) => response.data);
  }
);
export const PutUserCategory = createAsyncThunk(
  "UserCategory/put",
  async ({ body, id }) => {
    return await axios
      .put(`${API_URL}/user_category/${id}`, body)
      .then((response) => console.log(response.data));
  }
);

const UserCategorySlice = createSlice({
  name: "UserCategory",
  initialState: {
    postUserCategory: {
      Error: false,
      Loading: false,
      Success: false,
    },
    getUserCategory: {
      Error: false,
      Loading: false,
      Success: false,
      Data: [],
    },
    deleteUserCategory: {
      Error: false,
      Loading: false,
      Success: false,
    },
    putUserCategory: {
      Error: false,
      Loading: false,
      Success: false,
    },
  },
  extraReducers: {
    [PostUserCategory.pending]: (state, action) => {
      state.postUserCategory.Loading = true;
    },
    [PostUserCategory.fulfilled]: (state, action) => {
      state.postUserCategory.Error = false;
      state.postUserCategory.Success = true;
      state.postUserCategory.Loading = false;
    },
    [PostUserCategory.rejected]: (state, action) => {
      state.postUserCategory.Error = true;
      state.postUserCategory.Success = false;
      state.postUserCategory.Loading = false;
    },
    [GetUserCategory.pending]: (state, action) => {
      state.getUserCategory.Loading = true;
    },
    [GetUserCategory.fulfilled]: (state, action) => {
      state.getUserCategory.Error = false;
      state.getUserCategory.Success = true;
      state.getUserCategory.Loading = false;
      state.getUserCategory.Data = action.payload;
    },
    [GetUserCategory.rejected]: (state, action) => {
      state.getUserCategory.Error = true;
      state.getUserCategory.Success = false;
      state.getUserCategory.Loading = false;
      state.getUserCategory.Data = [];
    },
    [DeleteUserCategory.pending]: (state, action) => {
      state.deleteUserCategory.Loading = true;
    },
    [DeleteUserCategory.fulfilled]: (state, action) => {
      state.deleteUserCategory.Error = false;
      state.deleteUserCategory.Success = true;
      state.deleteUserCategory.Loading = false;
    },
    [DeleteUserCategory.rejected]: (state, action) => {
      state.deleteUserCategory.Error = true;
      state.deleteUserCategory.Success = false;
      state.deleteUserCategory.Loading = false;
    },
    [PutUserCategory.pending]: (state, action) => {
      state.putUserCategory.Loading = true;
    },
    [PutUserCategory.fulfilled]: (state, action) => {
      state.putUserCategory.Error = false;
      state.putUserCategory.Success = true;
      state.putUserCategory.Loading = false;
    },
    [PutUserCategory.rejected]: (state, action) => {
      state.putUserCategory.Error = true;
      state.putUserCategory.Success = false;
      state.putUserCategory.Loading = false;
    },
  },
});

export default UserCategorySlice.reducer;
