import styled from "styled-components";

export const Wrapper = styled.div`
  /* margin-left: 3%; */
  width: 100%;
  padding: 0% 3% 2%;
  .header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    button {
      padding: 10px 30px;
      background-color: rgba(3, 168, 158, 0.3);
      border-radius: 23px;
      color: #03a89e;
      font-weight: 700;
      letter-spacing: 1px;
      border: 1px solid rgba(3, 168, 158, 0.3);
      cursor: pointer;
    }
  }
  .cols {
    border: 1px solid rgba(3, 168, 158, 0.3);
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 3%;
    font-size: 25px;
    border-radius: 10px;
    height: 100px;
  }
  .buttons {
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin-top: 2%;
    .del {
      background-color: red;
      color: white;
      padding: 10px 20px;
      border: 1px solid red;
      border-radius: 10px;
      font-size: 18px;
      font-weight: 600;
      cursor: pointer;
    }
    .put {
      background-color: rgba(3, 168, 158, 0.8);
      color: white;
      padding: 10px 20px;
      border: 1px solid rgba(3, 168, 158, 0.8);
      border-radius: 10px;
      font-size: 18px;
      font-weight: 600;
      cursor: pointer;
    }
  }
`;
