import styled from "styled-components";

export const Post = styled.div`
  .buttons {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    .addimg {
      width: 100%;
      border: 1px dashed black;
      background-color: white;
      padding: 10px 0px;
      font-size: 20px;
    }
    .posst {
      padding: 10px 20px;
      cursor: pointer;
      font-size: 20px;
      background-color: #3a36db;
      color: white;
      border-radius: 10px;
      border: 1px solid #3a36db;
      margin-top: 5%;

      &:hover {
        background-color: #3a00c9;
      }
    }
    .post {
      padding: 10px 20px;
      cursor: pointer;
      font-size: 20px;
      background-color: #3a36db;
      color: white;
      border-radius: 10px;
      border: 1px solid #3a36db;
      margin-top: 5%;

      &:hover {
        background-color: #3a00c9;
      }
    }
  }
`;
