import Box from "@mui/material/Box";
import Modal from "@mui/material/Modal";
import { Post } from "./styled-index";
import { useDispatch, useSelector } from "react-redux";
import { UploadImage } from "../../../store/client/news";
import { useRef } from "react";

const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  bgcolor: "background.paper",
  border: "2px solid #000",
  boxShadow: 24,
  p: 4,
};

function PostNew({ open, handleClose, handleOpen, HandelPost }) {
  const dispatch = useDispatch();
  const inputFileRef = useRef(null);
  const onBtnClick = () => {
    inputFileRef.current.click();
  };
  const onFilechange = (e) => {
    dispatch(UploadImage(e));
  };
  const Loading = useSelector((state) => state.news.uploadProfile?.Loading);

  const Success = useSelector((state) => state.news.uploadProfile?.Success);

  return (
    <div>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <Post>
            <div className="header">
              <input
                type="file"
                style={{ display: "none" }}
                ref={inputFileRef}
                onChange={onFilechange}
              />
              <div className="buttons">
                <button className="addimg" onClick={onBtnClick}>
                  {Loading ? <>Loading...</> : <>Add</>}
                </button>
                <button className="post" onClick={HandelPost}>
                  Add Img
                </button>
              </div>
            </div>
          </Post>
        </Box>
      </Modal>
    </div>
  );
}

export default PostNew;
