import styled from "styled-components";

export const Table = styled.table`
  border-collapse: collapse;
  margin: 0 auto;
  width: 100%;

  tbody {
    background-color: white;
    td {
      text-align: center;
    }
    .put {
      padding: 10px 30px;
      background-color: rgba(3, 168, 158, 0.3);
      border-radius: 23px;
      color: #03a89e;
      font-weight: 700;
      letter-spacing: 1px;
      border: 1px solid rgba(3, 168, 158, 0.3);
      cursor: pointer;
      .bx {
        font-size: 20px;
      }
    }
    .delete {
      padding: 10px 30px;
      background-color: red;
      border-radius: 23px;
      color: white;
      font-weight: 700;
      letter-spacing: 1px;
      border: 1px solid red;
      cursor: pointer;
      .bx {
        font-size: 20px;
      }
    }
  }
`;
