import React from "react";
import { pdata } from "../fakeData";
import { AreaChart } from "recharts";
import { AreaChartWrapper, ChartHeader } from "./styled-index";
import { Tooltip } from "recharts";
import { CartesianAxis } from "recharts";
import { YAxis } from "recharts";
import { XAxis } from "recharts";
import { Area } from "recharts";

function AreaChartCompo() {
  return (
    <>
      <AreaChartWrapper>
        <ChartHeader>
          <h2>Reports</h2>
          <button>
            <i class="bx bx-dots-horizontal-rounded"></i>
          </button>
        </ChartHeader>
        <AreaChart
          width={595}
          height={280}
          data={pdata}
          margin={{ top: 10, right: -30, left: -25, bottom: 10 }}
        >
          <defs>
            <linearGradient id="total" x1="0" y1="0" x2="0" y2="1">
              <stop offset="-0.98%" stopColor="rgba(58, 54, 219, 0.5)" />
              <stop offset="100%" stopColor="rgba(255, 105, 180, 0.35)" />
            </linearGradient>
          </defs>
          <XAxis
            padding={{ right: 31, left: 10 }}
            allowDataOverflow={true}
            tickLine={false}
            dataKey="hour"
            stroke="#06152B"
            style={{
              fontFamily: "DM Sans",
              fontSize: "12px",
              opacity: "0.7",
            }}
            axisLine={false}
            strokeWidth="1"
          />
          <YAxis
            stroke="#06152B"
            style={{
              fontFamily: "DM Sans",
              fontSize: "12px",
              opacity: "0.7",
            }}
            axisLine={false}
            tickSize="1"
          />
          <CartesianAxis
            vertical={false}
            dataKey="student"
            strokeWidth="0.1"
            className="chartGrid"
          />
          <Tooltip
            isAnimationActive={true}
            offset={10}
            cursor={{ stroke: "#605CFF", strokeWidth: 2, strokeDasharray: 3 }}
            vertical
            contentStyle={{
              stroke: "#FF69B4",
              borderRadius: "10px",
              fontFamily: "DM Sans",
              fontSize: "12px",
            }}
          />
          <Area
            animationEasing="ease-in-out"
            activeDot={{ r: 5, stroke: "#FF69B4", strokeWidth: 2 }}
            dot={{ r: 4, stroke: "#FF69B4", strokeWidth: 2 }}
            baseLine={[{ x: 12, y: 15 }]}
            legendType="square"
            type="monotoneX"
            dataKey="student"
            stroke="#8884d8"
            fillOpacity={1}
            fill="url(#total)"
            width={650}
          />
        </AreaChart>
      </AreaChartWrapper>
    </>
  );
}

export default AreaChartCompo;
