import React from "react";
import { TableWrapper,Table, Thead, Tbody,TooltipButton, TooltipBtn } from "./styled-index";
import { column } from "../fakeData";
import Tooltip from "@mui/material/Tooltip";
import { ChartHeader } from "../AreaChart/styled-index";
const tableArr = [1, 2, 3, 4, 5];

function DashboardTable() {
  return (
    <>
      <TableWrapper>
      <ChartHeader>
          <h2>Recent Orders</h2>
          <button>
            <i class="bx bx-dots-horizontal-rounded"></i>
          </button>
        </ChartHeader>
        <Table>
          <Thead>
            <tr>
              {
                column.map(elem => (
                    <th key={elem.id}>
                        {elem.name}
                        <Tooltip
                      title={
                        <>
                          <TooltipBtn>
                            <button>Sort</button>
                            <button>Filter</button>
                          </TooltipBtn>
                        </>
                      }
                      arrow
                    >
                      <TooltipButton>
                        <i class="bx bxs-down-arrow"></i>
                      </TooltipButton>
                    </Tooltip>
                    </th>
                    
                ))
              }
            </tr>
          </Thead>
          <Tbody>
            {tableArr.map(() => (
              <tr>
                <td>#876364</td>
                <td>Camera Lens</td>
                <td>$178</td>
                <td>
                  <span>325</span>
                </td>
                <td>$1,46,660</td>
              </tr>
            ))}
          </Tbody>
        </Table>
      </TableWrapper>
    </>
  );
}

export default DashboardTable;
