import Box from "@mui/material/Box";
import Modal from "@mui/material/Modal";
import { Putt } from "./styled-index";
import { useDispatch, useSelector } from "react-redux";
import {
  GetNews,
  PutNews,
  PutUpload,
  UploadImage,
} from "../../../store/client/news";
import { useRef } from "react";

const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  bgcolor: "background.paper",
  border: "2px solid #000",
  boxShadow: 24,
  p: 4,
};

function PutModal({ open, handleClose, handleOpen, put_id }) {
  const dispatch = useDispatch();
  const inputFileRef = useRef(null);
  const onBtnClick = () => {
    inputFileRef.current.click();
  };
  const onFilechange = (e) => {
    dispatch(PutUpload(e));
  };
  const Loading = useSelector((state) => state.news.uploadPut?.Loading);
  const data = useSelector((state) => state.news.uploadPut?.data);
  const body = data;
  const HandelPut = async (e) => {
    await dispatch(PutNews({ body, id: put_id }));
    dispatch(GetNews());
    handleClose();
  };

  return (
    <div>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <Putt>
            <div className="header">
              <input
                type="file"
                style={{ display: "none" }}
                ref={inputFileRef}
                onChange={onFilechange}
              />
              <div className="buttons">
                <button className="addimg" onClick={onBtnClick}>
                  {Loading ? <>Loading...</> : <>Put</>}
                </button>
                <button className="put" onClick={HandelPut}>Put Img</button>
              </div>
            </div>
          </Putt>
        </Box>
      </Modal>
    </div>
  );
}

export default PutModal;
