import { Table, Tds } from "./styled-index";
import Img from "../../../assets/addadmin/48.png";
import { useDispatch, useSelector } from "react-redux";
import { useEffect } from "react";
import { GetAdmin } from "../../../store/client/admin";
function AdminList() {
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(GetAdmin());
  }, []);

  const Data = useSelector((state) => state?.admin?.getAdmin?.Data);
  return (
    <>
      <Table>
        <thead>
          <tr>
            <th>
              <h3>Invoice Id</h3>
            </th>
            <th>
              <h3>Name</h3>
            </th>
            <th>
              <h3>PhoneNumber</h3>
            </th>
          </tr>
        </thead>
        <tbody>
          {Data.map((elem, index) => (
            <tr>
              <td>
                <h3>{index}</h3>
              </td>
              <td>
                <h3>{elem.name}</h3>
              </td>
              <td>
                <h3>{elem.phone_number}</h3>
              </td>
            </tr>
          ))}
        </tbody>
      </Table>
    </>
  );
}

export default AdminList;
