import React from "react";
import InvoCard from "./InvoCard";
import InvoTopBar from "./InvoTopBar/index";
import AreaChartCompo from "./AreaChart";
import PieChartCompo from "./PieChart";
import { ChartWrapperBig } from "./styled-index";
import DashboardTable from "./DashboardTable";
import TopSelling from "./TopSelling";

function DashboardComponent() {
  return (
    <>
      <InvoTopBar />
      <InvoCard />
      <ChartWrapperBig>
        <AreaChartCompo />
        <PieChartCompo />
      </ChartWrapperBig>
      <ChartWrapperBig>
        <DashboardTable />
        <TopSelling />
      </ChartWrapperBig>
    </>
  );
}

export default DashboardComponent;
