import styled from "styled-components";

export const DeleteDiv = styled.div`
  text-align: center;
  .buttons {
    display: flex;
    justify-content: space-around;
    button {
      padding: 10px 20px;
      margin-top: 1%;
    }
    .del {
      background-color: red;
      border: 1px solid red;
      border-radius: 10px;
      color: white;
      font-size: 20px;
      font-weight: 600;
    }
    .put {
      background-color: #3a36db;
      border: 1px solid #3a36db;
      padding: 10px 20px;
      border-radius: 10px;
      color: white;
      font-weight: 600;
      font-size: 20px;
      cursor: pointer;
    }
  }
`;
