import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";
import { API_URL } from "../../servies";

export const GetUsers = createAsyncThunk("Users/get", async () => {
  return await axios.get(`${API_URL}/users`).then((response) => response.data);
});
export const PutUsers = createAsyncThunk("Users/put", async ({ body, id }) => {
  return axios
    .put(`${API_URL}/admin/updateUser/${id}`, body)
    .then((res) => res);
});


const UsersSlice = createSlice({
  name: "Users",
  initialState: {
    getUsers: {
      Error: false,
      Loading: false,
      Success: false,
      Data: [],
    },
    putUsers: {
      Error: false,
      Loading: false,
      Success: false,
    },
  },
  extraReducers: {
    [GetUsers.pending]: (state, action) => {
      state.getUsers.Loading = true;
    },
    [GetUsers.fulfilled]: (state, action) => {
      state.getUsers.Error = false;
      state.getUsers.Success = true;
      state.getUsers.Loading = false;
      state.getUsers.Data = action.payload;
    },
    [GetUsers.rejected]: (state, action) => {
      state.getUsers.Error = true;
      state.getUsers.Success = false;
      state.getUsers.Loading = false;
      state.getUsers.Data = [];
    },
    [PutUsers.pending]: (state, action) => {
      state.putUsers.Loading = true;
    },
    [PutUsers.fulfilled]: (state, action) => {
      state.putUsers.Error = false;
      state.putUsers.Success = true;
      state.putUsers.Loading = false;
      state.putUsers.Data = action.payload;
    },
    [PutUsers.rejected]: (state, action) => {
      state.putUsers.Error = true;
      state.putUsers.Success = false;
      state.putUsers.Loading = false;
    },
  },
});

export default UsersSlice.reducer;
