import { useRef, useState } from "react";
import { useDispatch } from "react-redux";
import { GetSuperUsers, PutSuperUsers } from "../../../store/client/superusers";

function PutSuperUser({ status, userId }) {
  const dispatch = useDispatch();
  const HandelFlase = async (e) => {
    const body = {
      status: false,
    };
    await dispatch(PutSuperUsers({ body, id: userId }));
    dispatch(GetSuperUsers());
  };
  const HandelTrue = async (e) => {
    const body = {
      status: true,
    };
    await dispatch(PutSuperUsers({ body, id: userId }));
    dispatch(GetSuperUsers());
  };
  return (
    <>
      {status == true ? (
        <>
          <button onClick={HandelFlase} value={"false"}>
          <i class='bx bx-check'></i>
          </button>
        </>
      ) : status == false ? (
        <>
          <button onClick={HandelTrue} value={"true"}>
            
          </button>
        </>
      ) : null}
    </>
  );
}

export default PutSuperUser;
