import React from "react";
import { PieChart, Pie, Tooltip, Cell } from "recharts";
import { data } from "../fakeData";
import { PieChartWrapper,Statis, StatisItem } from "./styled-index";
import { ChartHeader } from "../AreaChart/styled-index";

const COLORS = ["#3A36DB", "#fff", "#FF69B4", "#03A89E"];

function PieChartCompo() {
  return (
    <>
      <PieChartWrapper>
        <ChartHeader>
          <h2>Analytics</h2>
          <button>
            <i class="bx bx-dots-horizontal-rounded"></i>
          </button>
        </ChartHeader>
        <h3>80% <span>Transactions</span></h3>
        <PieChart
            width={420}
            height={219}
            margin={{ top: 10, right: 0, left: 30, bottom: 10 }}
         >
          <Pie
            data={data}
            cx={180}
            cy={100}
            innerRadius={60}
            outerRadius={80}
            // fill="#8884d8"
            borderRadius = {50}
            paddingAngle={2}
            dataKey="value"
          >
            {data.map((entry, index) => (
              <Cell
                key={`cell-${index}`}
                fill={COLORS[index % COLORS.length]}
              />
            ))}
          </Pie>
          <Tooltip
            isAnimationActive={true}
            offset={10}
            cursor={{ stroke: "#605CFF", strokeWidth: 2, strokeDasharray: 3 }}
            vertical
            contentStyle={{
              stroke: "#06152B",
              borderRadius: "10px",
              fontFamily: "DM Sans",
              fontSize: "12px",
            }}
          />
        </PieChart>
        <Statis>
            <StatisItem>
                <div></div>
                <p>Sale</p>
            </StatisItem>
            <StatisItem>
                <div></div>
                <p>Distribute</p>
            </StatisItem>
            <StatisItem>
                <div></div>
                <p>Return</p>
            </StatisItem>
        </Statis>
      </PieChartWrapper>
    </>
  );
}

export default PieChartCompo;
