import { style } from "@mui/system";
import styled from "styled-components";

export const PieChartWrapper = styled.div`
  width: 39%;
  background: #ffffff;
  border-radius: 10px;
  padding: 10px;
  position: relative;
  .recharts-wrapper{
    margin: 0 auto !important;
  }
  h3 {
    position: absolute;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-align-items: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
    font-family: "DM Sans";
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 40px;
    color: #06152b;
    margin: 0;
    width: 98%;
    top: 110px;
    span {
      font-family: "DM Sans";
      font-style: normal;
      font-weight: 500;
      font-size: 14px;
      line-height: 22px;
      color: #06152b;
      opacity: 0.7;
    }
  }
`;

export const Statis = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-around;
`;

export const StatisItem = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;

  div {
    width: 15px;
    height: 15px;
    border-radius: 5px;
    margin: 0 10px;
    cursor: pointer;

    &:nth-child(1) {
      background: #3a36db;
    }

    &:nth-child(2) {
      background: #03a89e;
    }

    &:nth-child(3) {
      background: #ff69b4;
    }
  }

  p {
    font-family: "DM Sans";
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 22px;
    color: #06152b;
    opacity: 0.7;
  }
`;
