import { Box, Modal } from "@mui/material";
import { useDispatch } from "react-redux";
import { DeleteCategory, GetCategory } from "../../../store/client/categories";
import { DeleteDiv } from "../../news/delete/styled-index";

const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  bgcolor: "background.paper",
  border: "2px solid #000",
  boxShadow: 24,
  p: 4,
};

function Delete({ open, handleClose, ids }) {
  const dispatch = useDispatch();

  const Delete = async (e) => {
    e.preventDefault();
    const id = ids;
    await dispatch(DeleteCategory(id));
    dispatch(GetCategory());
    handleClose();
  };

  return (
    <>
      <div>
        <Modal
          open={open}
          onClose={handleClose}
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description"
        >
          <Box sx={style}>
            <DeleteDiv>
              <h2>    ?</h2>
              <div className="buttons">
                <button className="del" onClick={Delete}>
                  
                </button>
                <button className="put" onClick={handleClose}>
                  
                </button>
              </div>
            </DeleteDiv>
          </Box>
        </Modal>
      </div>
    </>
  );
}

export default Delete;
