import { configureStore } from "@reduxjs/toolkit";
import NewsSlice from "./client/news";
import UsersSlice from "./client/users";
import SuperUsersSlice from "./client/superusers";
import CategorySlice from "./client/categories";
import UserCategorySlice from "./client/userCateg";
import AdminSlice from "./client/admin";
import LoginSlice from "./client/login";

export const store = configureStore({
  reducer: {
    news: NewsSlice,
    users: UsersSlice,
    superusers: SuperUsersSlice,
    category: CategorySlice,
    userCateg: UserCategorySlice,
    admin: AdminSlice,
    login: LoginSlice,
  },
});
