import AllRouterAdmin from "./routers";
// import AllRouter from "./routes/index";
import { useLocation } from "react-router-dom";
import Sidebar from "./components/sidebar";
import { GlobalStyle, Main } from "./App-styled";
import { RoutersDataAdmin } from "./routers/data-router";
function App() {
  const pathname = useLocation();
  const filter = RoutersDataAdmin.filter((e) => e.path === pathname.pathname);
  window.localStorage.setItem(
    "pathname",
    filter.map((elem) => elem.path)
  );
  return (
    <>
      {pathname.pathname === "/" ? (
        <AllRouterAdmin />
      ) : null &&
        pathname.pathname === window.localStorage.getItem("pathname") ? (
        <Main>
          <GlobalStyle whiteColor />
          <Sidebar />
          <AllRouterAdmin />
        </Main>
      ) : (
        <Main>
          <GlobalStyle whiteColor />
          <Sidebar />
          <AllRouterAdmin />
        </Main>
      )}
    </>
  );
}

export default App;
