import styled from "styled-components";

export const AreaChartWrapper = styled.div`
  width: 55%;
  max-width: 600px;
  padding: 5px 10px;
  background: #ffffff;
  border-radius: 10px;
`;

export const ChartHeader = styled.div`
 display: flex;
 align-items: center;
 justify-content: space-between;

 h2{
    font-family: 'DM Sans';
    font-weight: 500;
    font-size: 16px;
    line-height: 25px;
    color: #06152B;
    margin: 3px 0;
   }

   button{
    color: #06152B;
    font-size: 20px;
    border: none;
    outline: none;
    background: none;
    cursor: pointer
   }
`;
