import styled from "styled-components";

export const Form = styled.div`
  form {
    display: flex;
    flex-direction: column;

    input {
      border: 1px solid rgba(3, 168, 158, 0.9);
      padding: 15px 20px;
      outline: none;
      border-radius: 10px;
    }
    button {
      padding: 10px 30px;
      background-color: rgba(3, 168, 158, 0.3);
      border-radius: 10px;
      margin-top: 5%;
      color: #03a89e;
      font-weight: 700;
      letter-spacing: 1px;
      border: 1px solid rgba(3, 168, 158, 0.3);
      cursor: pointer;
    }
  }
`;
