import { useDispatch, useSelector } from "react-redux";
import Modal from "@mui/material/Modal";
import Box from "@mui/material/Box";
import { Form } from "./styled-index";
import { useRef } from "react";
import { GetAdmin, PostAdmin } from "../../../store/client/admin";

const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  bgcolor: "background.paper",
  border: "2px solid #000",
  boxShadow: 24,
  p: 4,
};

function Post({ open, handleClose }) {
  const categ = useRef("");
  const number = useRef();
  const password = useRef();
  const dispatch = useDispatch();
  const HandelPost = async (e) => {
    e.preventDefault();
    const body = {
      name: categ.current.value,
      phone_number: number.current.value,
      password: password.current.value,
    };
    await dispatch(PostAdmin(body));
    dispatch(GetAdmin());
    handleClose();
  };

  return (
    <>
      <div>
        <Modal
          open={open}
          onClose={handleClose}
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description"
        >
          <Box sx={style}>
            <Form>
              <form>
                <input
                  ref={categ}
                  type="text"
                  placeholder="admin name..."
                  required
                />
                <input
                  ref={number}
                  type="number"
                  placeholder="admin number..."
                  required
                />
                <input
                  ref={password}
                  type="password"
                  placeholder="admin password..."
                  required
                />
                <button onClick={HandelPost}>Add</button>
              </form>
            </Form>
          </Box>
        </Modal>
      </div>
    </>
  );
}

export default Post;
