import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";
import { API_URL } from "../../servies";

export const GetSuperUsers = createAsyncThunk("SuperUsers/get", async () => {
  return await axios
    .get(`${API_URL}/superUser`)
    .then((response) => response.data);
});

export const PutSuperUsers = createAsyncThunk(
  "SuperUsers/put",
  async ({ body, id }) => {
    return axios
      .put(`${API_URL}/admin/updateSuperUser/${id}`, body)
      .then((res) => res);
  }
);

export const DeleteSuperUsers = createAsyncThunk("SuperUsers/delete", async (id) => {
  return await axios
    .delete(`${API_URL}/superUser/${id}`)
    .then((response) => response.data);
});
const SuperUsersSlice = createSlice({
  name: "SuperUsers",
  initialState: {
    getSuperUsers: {
      Error: false,
      Loading: false,
      Success: false,
      Data: [],
    },
    putSuperUsers: {
      Error: false,
      Loading: false,
      Success: false,
    },
    deleteSuperUsers: {
      Error: false,
      Loading: false,
      Success: false,
    },
  },
  extraReducers: {
    [GetSuperUsers.pending]: (state, action) => {
      state.getSuperUsers.Loading = true;
    },
    [GetSuperUsers.fulfilled]: (state, action) => {
      state.getSuperUsers.Error = false;
      state.getSuperUsers.Success = true;
      state.getSuperUsers.Loading = false;
      state.getSuperUsers.Data = action.payload;
    },
    [GetSuperUsers.rejected]: (state, action) => {
      state.getSuperUsers.Error = true;
      state.getSuperUsers.Success = false;
      state.getSuperUsers.Loading = false;
      state.getSuperUsers.Data = [];
    },
    [PutSuperUsers.pending]: (state, action) => {
      state.putSuperUsers.Loading = true;
    },
    [PutSuperUsers.fulfilled]: (state, action) => {
      state.putSuperUsers.Error = false;
      state.putSuperUsers.Success = true;
      state.putSuperUsers.Loading = false;
      state.putSuperUsers.Data = action.payload;
    },
    [PutSuperUsers.rejected]: (state, action) => {
      state.putSuperUsers.Error = true;
      state.putSuperUsers.Success = false;
      state.putSuperUsers.Loading = false;
    },
    [DeleteSuperUsers.pending]: (state, action) => {
      state.deleteSuperUsers.Loading = true;
    },
    [DeleteSuperUsers.fulfilled]: (state, action) => {
      state.deleteSuperUsers.Error = false;
      state.deleteSuperUsers.Success = true;
      state.deleteSuperUsers.Loading = false;
    },
    [DeleteSuperUsers.rejected]: (state, action) => {
      state.deleteSuperUsers.Error = true;
      state.deleteSuperUsers.Success = false;
      state.deleteSuperUsers.Loading = false;
    },
  },
});

export default SuperUsersSlice.reducer;
