import { useDispatch, useSelector } from "react-redux";
import Modal from "@mui/material/Modal";
import Box from "@mui/material/Box";
import { Form } from "./styled-index";
import { useRef } from "react";
import { GetCategory, PutCategory } from "../../../store/client/categories";

const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  bgcolor: "background.paper",
  border: "2px solid #000",
  boxShadow: 24,
  p: 4,
};

function PutModal({ open, handleClose, putId }) {
  const categ = useRef("");
  const dispatch = useDispatch();
  const HandelPut = async (e) => {
    e.preventDefault();
    const body = {
      title: categ.current.value,
    };
    await dispatch(PutCategory({ body, id: putId }));
    dispatch(GetCategory());
    handleClose();
  };

  return (
    <>
      <div>
        <Modal
          open={open}
          onClose={handleClose}
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description"
        >
          <Box sx={style}>
            <Form>
              <form>
                <input
                  ref={categ}
                  type="text"
                  placeholder="usercategory name..."
                  //   required
                />
                <button onClick={HandelPut}>Add</button>
              </form>
            </Form>
          </Box>
        </Modal>
      </div>
    </>
  );
}

export default PutModal;
