import { useRef, useState } from "react";
import { useDispatch } from "react-redux";
import { GetUsers, PutUsers } from "../../../store/client/users";

function PutUser({ status, userId }) {
  const dispatch = useDispatch();
  const HandelFlase = async (e) => {
    const body = {
      status: false,
    };
    await dispatch(PutUsers({ body, id: userId }));
    dispatch(GetUsers());
  };
  const HandelTrue = async (e) => {
    const body = {
      status: true,
    };
    await dispatch(PutUsers({ body, id: userId }));
    dispatch(GetUsers());
  };
  return (
    <>
      {status == true ? (
        <>
          <button className="put" onClick={HandelFlase} value={"false"}>
            <i class="bx bx-check"></i>
          </button>
        </>
      ) : status == false ? (
        <>
          <button className="put" onClick={HandelTrue} value={"true"}>
            
          </button>
        </>
      ) : null}
    </>
  );
}

export default PutUser;
