import { useEffect, useState } from "react";
import { Col, Row } from "react-grid-system";
import { useDispatch, useSelector } from "react-redux";
import { GetCategory } from "../../store/client/categories";
import PutModal from "./CategPut";
import Delete from "./delete";
import Post from "./post";
import { Wrapper } from "./styled-index";

function CategoriesComp() {
  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const [open1, setOpen1] = useState(false);
  const handleOpen1 = () => setOpen1(true);
  const handleClose1 = () => setOpen1(false);
  const [userId, setUserId] = useState();

  const [open2, setOpen2] = useState(false);
  const handleOpen2 = () => setOpen2(true);
  const handleClose2 = () => setOpen2(false);
  const [putId, setPutId] = useState();

  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(GetCategory());
  }, []);
  const Data = useSelector((state) => state.category?.getCategory?.Data);
  return (
    <>
      <Wrapper>
        <div className="header">
          <h2>Categories</h2>
          <button onClick={handleOpen}>Add+</button>
        </div>
        <Post open={open} handleClose={handleClose} />
        <Row styled={{ margin: 0 }}>
          {Data.map((elem, index) => (
            <Col lg={4} key={index}>
              <div className="cols">{elem.title}</div>
              <div className="buttons">
                <button
                  onClick={() => {
                    setUserId(elem.id);
                    handleOpen1();
                  }}
                  className="del"
                >
                  Delete
                </button>
                <button
                  onClick={() => {
                    handleOpen2();
                    setPutId(elem.id);
                  }}
                  className="put"
                >
                  Put
                </button>
              </div>
            </Col>
          ))}
        </Row>
        <Delete open={open1} handleClose={handleClose1} ids={userId} />
        <PutModal
          open={open2}
          setOpen={setOpen2}
          handleClose={handleClose2}
          handleOpen={handleOpen2}
          putId={putId}
        />
      </Wrapper>
    </>
  );
}

export default CategoriesComp;
