import { RoutersDataAdmin } from "./data-router";
import { Routes, Route } from "react-router-dom";
function AllRouterAdmin() {
  return (
    <>
      <Routes>
        {RoutersDataAdmin.map((elem) => (
          <>
            <Route path={elem.path} element={elem.Element} key={elem.id} />
          </>
        ))}
      </Routes>
    </>
  );
}

export default AllRouterAdmin;
