import styled from "styled-components";

export const Wrapper = styled.div`
  width: 100%;
  .header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 5%;
    button {
      background-color: #3a36db;
      border: 1px solid #3a36db;
      padding: 10px 20px;
      border-radius: 10px;
      color: white;
      font-size: 18px;
      cursor: pointer;
    }
  }
  img {
    width: 100%;
    height: 400px;
    border-radius: 10px;
  }
  .imgs {
    .cols {
      margin-top: 3%;
    }
    .buttons {
      display: flex;
      justify-content: space-around;
      button {
        padding: 10px 20px;
        margin-top: 1%;
      }
      .del {
        background-color: red;
        border: 1px solid red;
        border-radius: 10px;
        color: white;
        font-size: 20px;
        font-weight: 600;
      }
      .put {
        background-color: #3a36db;
        border: 1px solid #3a36db;
        padding: 10px 20px;
        border-radius: 10px;
        color: white;
        font-weight: 600;
        font-size: 20px;
        cursor: pointer;
      }
    }
  }
`;
