import styled from "styled-components";

export const TableWrapper = styled.div`
  width: 55%;
  max-width: 600px;
  padding: 5px 10px;
  background: #ffffff;
  border-radius: 10px;
`;

export const Table = styled.table`
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
`;

export const Thead = styled.thead`
  width: 100%;
  tr {
    padding: 10px;
    border-bottom: 1px solid #f2f3f4;

    th {
      padding: 10px;
      font-family: "DM Sans";
      font-style: normal;
      font-weight: 400;
      font-size: 13.3523px;
      line-height: 17px;
      color: #06152b;
    }
  }
`;

export const Tbody = styled.tbody`
  width: 100%;
  tr {
    padding: 10px;
    border-collapse: collapse;
    border: none;

    &:nth-child(2n) {
      background: #f1f4fa;
      border-radius: 10px;
    }

    td {
      padding: 10px;
      text-align: center;
      font-family: "DM Sans";
      font-style: normal;
      font-weight: 400;
      font-size: 13.3523px;
      line-height: 17px;
      color: #06152b;

      span {
        background: #cdd0d5;
        border-radius: 8.45081px;
        padding: 5px 15px;
        color: #3a36db;
      }
    }
  }
`;

export const TooltipBtn = styled.div`
  padding: 5px 10px button {
    outline: none;
    border: none;
    background: #3a36db;
    border-radius: 10px;
    display: flex;
    align-items: center;
    flex-direction: column;
  }
`;

export const TooltipButton = styled.button`
  border: none;
  outline: none;
  cursor: pointer;
  background: none;
  color: #cdd0d5;
  margin-bottom: -5px;
`;
