import React from "react";
import { SellingWrapper,SellingCard, SellingInfo } from "./styled-index";
import {ChartHeader} from '../AreaChart/styled-index'

import nike from '../../../assets/nike.png'
import star from '../../../assets/star.png'

function TopSelling() {
  return (
    <>
      <SellingWrapper>
        <ChartHeader>
          <h2>Top selling Products</h2>
          <button>
            <i class="bx bx-dots-horizontal-rounded"></i>
          </button>
        </ChartHeader>
        <SellingCard>
            <img src={nike} alt="nike" width={95} height={95} />
            <SellingInfo>
                <h2>NIKE Shoes Black Pattern</h2>
                <img src={star} alt="star" width={87} height={14} />
                <p>$87</p>
            </SellingInfo>
        </SellingCard>
         <SellingCard>
            <img src={nike} alt="nike" width={95} height={95} />
            <SellingInfo>
                <h2>NIKE Shoes Black Pattern</h2>
                <img src={star} alt="star" width={87} height={14} />
                <p>$87</p>
            </SellingInfo>
        </SellingCard>
      </SellingWrapper>
    </>
  );
}

export default TopSelling;
