import styled from "styled-components";

export const Heading = styled.div`
display: flex;
align-items: center;
justify-content: space-between;
h2 {
    letter-spacing: 1px ;
}

div{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 2%;

    input {
        background-color: white;
        padding: 10px 40px 10px 20px;
        border: 1px solid white;
        outline: none;
        border-radius: 10px;
    }
    button {
        background-color: #3A36DB;
        color: white;
        border: 1px solid #3A36DB;
        border-radius: 10px;
        padding: 10px 20px;
        font-weight: 700;
        letter-spacing: 1px;
        cursor: pointer;
        transition: .65s;

        &:hover {
            background-color: white;
            color: #3A36DB;

        }
    }
}
`