import Box from "@mui/material/Box";
import Modal from "@mui/material/Modal";
import { DeleteDiv } from "./styled-index";
import { useDispatch, useSelector } from "react-redux";
import {
  DeleteSuperUsers,
  GetSuperUsers,
} from "../../../store/client/superusers";

const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  bgcolor: "background.paper",
  border: "2px solid #000",
  boxShadow: 24,
  p: 4,
};

function DeleteModal({ open, setOpen, handleClose, handleOpen, delid }) {
  const dispatch = useDispatch();
  const id = delid;
  const Delete = async (e) => {
    e.preventDefault();
    await dispatch(DeleteSuperUsers(id));
    dispatch(GetSuperUsers());
    handleClose();
  };
  return (
    <div>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <DeleteDiv>
            <h2>    ?</h2>
            <div className="buttons">
              <button className="del" onClick={Delete}>
                
              </button>
              <button className="put" onClick={handleClose}>
                
              </button>
            </div>
          </DeleteDiv>
        </Box>
      </Modal>
    </div>
  );
}

export default DeleteModal;
